<script>
import { Line } from 'vue-chartjs'
export default {
  extends: Line,
  props: ['dataset'],
  mounted () {
    var data = this.dataset
    //       .
    this.renderChart({
      labels: [' 2018', ' 2018', ' 2018', ' 2018', ' 2018', ' 2018', ' 2018', ' 2018', ' 2018', ' 2018', ' 2018', ' 2018'],
      datasets: data
    },
    {
      responsive: true,
      maintainAspectRatio: false,
      scales: {
        yAxes: [{
          ticks: {
            beginAtZero: true,
            stepSize: 50,
            min: 0
          }
        }]
      },
      legend: {
        position: 'bottom',
        labels: {
          boxWidth: 13,
          fontColor: '#333',
          usePointStyle: true
        }
      },
      layout: {
        padding: 20
      }
    })
  }
}
</script>



