<template lang="pug">
  .curator
    .curator__breadcrumbs.breadcrumbs__block
      app-breadcrumbs(:data="breadcrumbs")
    .curator__header
      .curator__title
        app-page-title(:title="title")
    .curator__content
      .curator__btn
        app-button(:info="btn")
      .curator__table
        app-table(:table="table")
</template>

<script>
import AppBreadcrumbs from '@/components/Breadcrumbs'
import AppPageTitle from '@/components/PageTitle'
import AppButton from '@/components/Button'
import AppTable from '@/components/Table'

export default {
  name: 'AppCurator',
  components: {
    AppBreadcrumbs,
    AppPageTitle,
    AppButton,
    AppTable
  },
  data () {
    return {
      breadcrumbs: [
        {
          text: ' ',
          href: '#'
        },
        {
          text: '',
          href: '#'
        }
      ],
      title: '',
      btn: {
        text: ' '
      },
      table: {
        title: '',
        select: {
          filter: ' ',
          data: [
            {
              val: ' '
            },
            {
              val: ' '
            },
            {
              val: ' '
            },
            {
              val: ' - '
            }
          ]
        },
        thead: [
          {
            val: '',
            key: 'fio'
          },
          {
            val: '',
            key: 'post'
          },
          {
            val: ' ',
            key: 'activity'
          },
          {
            val: '',
            key: 'no'
          },
          {
            val: ' .',
            key: 'month'
          },
          {
            val: ' .',
            key: 'cvart'
          },
          {
            val: '() ',
            key: 'address'
          }
        ],
        tbody: [
          {
            fio: ' .',
            post: ' ',
            activity: '0',
            no: '4',
            month: '2/4',
            cvart: '12/24',
            address: ',  -, 15 (  )'
          },
          {
            fio: ' .',
            post: ' ',
            activity: '4',
            no: '8',
            month: '2/4',
            cvart: '12/24',
            address: ',  -, 15 (  )'
          },
          {
            fio: ' .',
            post: ' ',
            activity: '0',
            no: '4',
            month: '2/4',
            cvart: '12/24',
            address: ',  -, 15 (  )'
          },
          {
            fio: ' .',
            post: ' ',
            activity: '0',
            no: '4',
            month: '2/4',
            cvart: '12/24',
            address: ',  -, 15 (  )'
          },
          {
            fio: ' .',
            post: ' - ',
            activity: '0',
            no: '4',
            month: '2/4',
            cvart: '12/24',
            address: ',  -, 15 (  )'
          },
          {
            fio: ' .',
            post: ' ',
            activity: '2',
            no: '4',
            month: '2/4',
            cvart: '12/24',
            address: ',  -, 15 (  )'
          },
          {
            fio: ' .',
            post: ' ',
            activity: '0',
            no: '4',
            month: '2/4',
            cvart: '12/24',
            address: ',  -, 15 (  )'
          },
          {
            fio: ' .',
            post: ' - ',
            activity: '0',
            no: '4',
            month: '2/4',
            cvart: '12/24',
            address: ',  -, 15 (  )'
          }
        ]
      }
    }
  }
}
</script>



