/* eslint-disable */
export default {
  namespaced: true,
  state: {
    items: [
      {
        href: '/',
        text: ' ',
        icon: 'static/img/general/nav-statistics.png',
        mod: 'base'
      },
      {
        href: '/bid',
        text: '',
        icon: 'static/img/general/nav-bid.png',
        mod: 'arrow',
        sublist: [
          {
            city: '',
            eng: 'activity'
          },
          {
            city: '',
            eng: 'draft'
          },
          {
            city: '',
            eng: 'ready'
          }
        ],
        bid: true
      },
      {
        href: '/object',
        text: '',
        icon: 'static/img/general/nav-object.png',
        mod: 'arrow',
        sublist: [
          {
            city: '',
            eng: 'volgograd'
          },
          {
            city: '',
            eng: 'moscow'
          },
          {
            city: '',
            eng: 'tambov'
          },
          {
            city: '-',
            eng: 'sankt-peterburg'
          },
          {
            city: '',
            eng: 'tumen'
          }
        ],
        subitems: [
          {
            object: '',
            eng: 'shop'
          },
          {
            object: '',
            eng: 'office'
          },
          {
            object: '',
            eng: 'stock'
          }
        ]
      },
      {
        href: '/curator',
        text: '',
        icon: 'static/img/general/nav-curator.png',
        mod: 'base'
      },
      {
        href: '/colloborator',
        text: ' ',
        icon: 'static/img/general/nav-collaborator.png',
        mod: 'base'
      },
      {
        href: '/letter',
        text: '',
        icon: 'static/img/general/nav-letter.png',
        mod: 'letter'
      },
      {
        href: '/settings',
        text: '',
        icon: 'static/img/general/nav-settings.png',
        mod: 'base'
      }
    ]
  },
  getters: {
    items (state) {
      return state.items
    }
  }
}


